export const getStaticPaths = async () => {
  const posts = (await import("../../api/news.json")).default;
  // const slugs = posts.map((post) => post.id);
  const paths = posts.map((slug) => ({ params: { id: slug.id.toString() } }));
  return {
    paths,
    fallback: false,
  };
};

export const getStaticProps = async ({ params: { slug } }) => {
  const posts = (await import("../../api/news.json")).default;
  const post = posts.find((x) => x.slug === slug);

  console.log(post);
  return {
    props: {
      post,
    },
  };
};

export default Post;
